* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}
.container {
  width: 100%;
  height: 100%;
  max-width: 1170px;
  min-width: 320px;
  z-index: 2;
}
header {
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 83px;
  display: flex;
  justify-content: center;
}
header .container {
  display: flex;
  justify-content: flex-end;
}
header .container .logo {
  display: flex;
  align-items: center;
  margin-right: 87px;
}
header .container .logo #image {
  margin-right: 17px;
}
header .info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 650px;
}
header .info .item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
header .info .item img {
  margin-right: 12px;
}
header .info .item p {
  text-align: left;
}
main .first {
  width: 100%;
  height: 720px;
  position: relative;
  display: flex;
  justify-content: center;
}
main .first .container {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  z-index: 2;
  position: absolute;
}
main .first .container .img-container {
  position: absolute;
  left: -220px;
  width: 760px;
  height: 835px;
  margin-right: 14px;
}
main .first .container .img-container .legs {
  z-index: 1;
  position: absolute;
  width: 700px;
}
main .first .container .img-container .circle-are {
  z-index: 0;
  width: 100%;
  height: 100%;
  top: 7%;
  position: relative;
}
main .first .container .img-container .circle-are .circle-border {
  position: absolute;
  top: 8%;
  right: 22%;
}
main .first .container .img-container .circle-are .circle {
  position: absolute;
  top: 15%;
  right: 10%;
  width: 571px;
  height: 572px;
  border-radius: 50%;
  background-image: url("../images/background.png");
  background-size: cover;
}
main .first .container .info-block {
  width: 370px;
  height: 520px;
  font-family: Roboto, sans-serif;
  margin-right: 40px;
}
main .first .container .info-block .title {
  margin-bottom: 47px;
}
main .first .container .info-block .title h1 {
  width: 300px;
  font-size: 44px;
  margin-bottom: 0;
}
main .first .container .info-block .title .underline {
  font-size: 34px;
  font-weight: 900;
}
main .first .container .info-block .title .orange {
  position: relative;
  z-index: 1;
}
main .first .container .info-block .title .orange:after {
  z-index: -1;
  content: "";
  height: 5px;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 1px;
  background-color: #ff6b00;
}
main .first .container .info-block .title .green {
  position: relative;
  z-index: 1;
  margin-bottom: 47px;
}
main .first .container .info-block .title .green:after {
  z-index: -1;
  content: "";
  height: 5px;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 1px;
  background-color: #a7c93d;
}
main .first .container .info-block .points {
  display: flex;
  flex-direction: column;
  background-image: url("../images/arrow-plus.png");
  background-repeat: no-repeat;
  background-position: 25px 62px;
}
main .first .container .info-block .points .point {
  font-family: Roboto, sans-serif;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
main .first .container .info-block .points .point:not(:last-child) {
  margin-bottom: 40px;
}
main .first .container .info-block .points .point:first-child .circle:after {
  content: "2в1";
}
main .first .container .info-block .points .point:nth-child(2) .circle:after {
  content: "1";
}
main .first .container .info-block .points .point:last-child .circle:after {
  content: "2";
}
main .first .container .info-block .points .point .circle {
  border-radius: 50%;
  width: 60px;
  height: 60px;
  padding: 12px 20px;
  margin-right: 20px;
  background-color: #a7c93d;
  border: 7px solid #e8edc9;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
main .first .container .order-form {
  width: 270px;
  height: 555px;
  background-color: white;
  border: 7px solid #a8ca3f;
  border-radius: 15px;
  position: relative;
}
main .first .container .order-form .free {
  display: flex;
  padding: 0 11px 13px 11px;
  position: absolute;
  border: 6px solid #a8ca3f;
  border-top: none;
  border-radius: 0 0 10px 10px;
  left: 50%;
  top: -7px;
  background-color: white;
  transform: translate(-50%, 0);
  font-size: 16px;
}
main .first .container .order-form .free img {
  margin-right: 9px;
}
main .first .container .order-form .content {
  height: 100%;
  padding: 48px 18px 0px 18px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
}
main .first .container .order-form .content p {
  font-size: 20px;
  text-align: center;
}
main .first .container .order-form .content p span {
  color: #ff6b00;
}
main .first .container .order-form .content .prices {
  display: flex;
  margin-bottom: 15px;
}
main .first .container .order-form .content .prices .old-price {
  margin-right: 18px;
}
main .first .container .order-form .content .prices .old-price .old {
  font-size: 14px;
  color: #8f8f8f;
  margin-bottom: 18px;
}
main .first .container .order-form .content .prices .old-price .cross {
  position: relative;
}
main .first .container .order-form .content .prices .old-price .cross p {
  color: black;
  font-size: 34px;
  z-index: 1;
}
main .first .container .order-form .content .prices .old-price .cross .mark {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url("../images/cross.png");
  background-repeat: no-repeat;
  background-position: center;
  z-index: 2;
}
main .first .container .order-form .content .prices .new-price p {
  font-size: 14px;
  color: black;
}
main .first .container .order-form .content .prices .new-price p:first-child {
  margin-bottom: 10px;
}
main .first .container .order-form .content .prices .new-price p:last-child {
  font-size: 34px;
  border-radius: 5px;
  padding: 8px 10px;
  background-color: #ffc002;
}
main .first .container .order-form .content .form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
main .first .container .order-form .content .form input {
  width: 100%;
  height: 65px;
  margin-bottom: 10px;
  border-radius: 35px;
  outline: none;
  border: none;
  background-color: #eef2f7;
  padding-left: 55px;
  position: relative;
}
main .first .container .order-form .content .form #man {
  background-image: url("../images/man.png");
  background-repeat: no-repeat;
  background-position: 21px center;
}
main .first .container .order-form .content .form #phone {
  background-image: url("../images/phone.png");
  background-repeat: no-repeat;
  background-position: 21px center;
  margin-bottom: 15px;
}
main .first .container .order-form .content .form #button {
  width: 100%;
  height: 70px;
  border-radius: 35px;
  background-color: #ffc002;
  outline: none;
  border: none;
  position: relative;
  font-size: 16px;
  font-weight: 900;
  margin-bottom: 20px;
}
main .first .container .order-form .content .form #button:after {
  position: absolute;
  content: url("../images/border-in.png");
  top: 5px;
  bottom: 0;
  left: 0;
  right: 0;
}
main .first .container .order-form .content .form #button span {
  font-size: 28px;
}
main .first .container .order-form .content .lost {
  margin-bottom: 15px;
}
main .first .container .order-form .content .lost #green {
  color: #9dbd39;
}
main .first .container .order-form .content .protect {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 44px;
  bottom: 0;
  background-color: #eef2f7;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
main .first .container .order-form .content .protect img {
  margin-right: 11px;
}
main .first .container .order-form .content .protect p {
  color: #8f8f8f;
  font-size: 14px;
}
main .first .background-img {
  width: 100%;
  height: 100%;
  background-color: #e2e2e2;
}
main .first .background-img .image {
  z-index: 0;
  position: absolute;
  left: 50%;
  right: 57%;
  height: 100%;
  background-image: url("../images/background.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left;
}
main .first .background-img .translucent-block {
  z-index: 1;
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url("../images/blue-back.png");
  opacity: 0.7;
}
